body {
  color: #3d3d3d;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 150%;
}

h1, p {
  margin: 0;
  padding: 0;
}

.container {
  min-height: 80px;
  background-color: #f0f8ff;
  border-radius: 4px;
  margin-left: 16px;
  margin-right: 16px;
  padding: 20px;
  box-shadow: 0 15px 25px rgba(0, 0, 0, .15), 0 5px 10px rgba(0, 0, 0, .05);
}

.loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border-top: 4px solid #87cefa;
  border-right: 4px solid rgba(0, 0, 0, 0);
  border-radius: 50%;
  margin-top: 40px;
  animation: rotation 1s linear infinite;
  display: inline-block;
}

.loader:after {
  content: "";
  box-sizing: border-box;
  width: 48px;
  height: 48px;
  border-bottom: 4px solid #00bfff;
  border-left: 4px solid rgba(0, 0, 0, 0);
  border-radius: 50%;
  position: absolute;
  top: 0;
  left: 0;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.cat-info {
  margin-top: 15px;
  display: flex;
}

.cat-image {
  width: 300px;
  height: auto;
  border-radius: 4px;
  display: block;
  box-shadow: 0 15px 25px rgba(0, 0, 0, .15), 0 5px 10px rgba(0, 0, 0, .05);
}

.cat-desc {
  margin: 15px 0 0 15px;
}

.cat-spycie, .cat-description {
  margin-bottom: 10px;
}

